@import './variables';
@import './libs/normalize';

@import './components/all';

body {
  background: $background-rgb;
  background: $background;
}

.wrapper {
  width: calc(100vw - 100px);
  height: 100%;
  background-color: #fff;
  margin: 50px auto;
  border-radius: 30px;
  max-width: 1400px;

  @media(max-width: 970px) {
    width: calc(100vw - 40px);
    margin-top: 30px;
  }

  @media(max-width: 680px) {
    width: calc(100vw - 20px);
    margin-top: 30px;
  }
}

.content {
  padding: 40px 0;

  &__error-info {
    margin: 100px auto;
    max-width: 500px;
    text-align: center;

    h2 {
      font-size: 32px;
      font-weight: 700;
      margin-bottom: 10px;
    }

    p {
      font-size: 20px;
    }
  }

  &__title {
    margin: 40px 0;
    font-size: 36px;
    font-weight: 700;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-column-gap: 11px;

    @media(max-width: 1350px) {
      grid-template-columns: repeat(3, 1fr);
    }

    @media(max-width: 1060px) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media(max-width: 740px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media(max-width: 1295px) {
      flex-direction: column;
    }
  }
}

.container {
  width: $container-width;
  margin: 0 auto;

  &--cart {
    max-width: 820px;
    margin: 90px auto;

    @media(max-width: 600px) {
      margin: 30px auto;
    }

    .content__title {
      margin: 0;
    }
  }
}

.cart {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .content__title {
      @media(max-width: 390px) {
        margin-bottom: 10px;
      }
    }

    @media(max-width: 390px) {
      flex-direction: column;
    }
  }

  .content__title {
    display: flex;
    align-items: center;
    font-size: 27px;

    svg {
      position: relative;
      top: -2px;
      width: 30px;
      height: 30px;
      margin-right: 10px;

      path {
        stroke: $black;
      }
    }
  }

  &__clear {
    display: flex;
    align-items: center;
    cursor: pointer;
    @include noselect();

    span {
      display: inline-block;
      margin-left: 7px;
      color: #b6b6b6;
      font-size: 17px;
    }

    span,
    svg,
    path {
      transition: all $duration ease-in-out;
    }

    &:hover {
      svg {
        path {
          stroke: darken($color: #b6b6b6, $amount: 50);
        }
      }

      span {
        color: darken($color: #b6b6b6, $amount: 50);
      }
    }
  }

  .content__items {
    display: block;
    margin-top: 36px;
  }

  &__item {
    display: flex;
    width: 100%;
    border-top: 1px solid $grey;
    padding-top: 15px;
    margin-top: 15px;
    position: relative;

    @media(max-width: 885px) {
      justify-content: space-between;
    }

    @media(max-width: 600px) {
      flex-direction: column;
      align-items: center;
    }

    &-img {
      display: flex;
      align-items: center;
      margin-right: 15px;
      width: 100px;
      justify-content: center;

      @media(max-width: 410px) {
        width: 70px;
      }

      img {
        height: 80px;
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      justify-content: center;

      @media(max-width: 960px) {
        margin-right: 7px;
      }

      h3 {
        font-weight: bold;
        font-size: 19px;
        line-height: 27px;
        letter-spacing: 0.01em;
      }

      p {
        font-size: 17px;
        color: #8d8d8d;
      }
    }

    &-count {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 110px;

      @media(max-width: 600px) {
        margin-bottom: 10px;
      }

      &-minus {
        svg {
          path:first-of-type {
            display: none;
          }
        }

        &:disabled {
          opacity: 0.4;
          border-color: #787878;

          &:active {
            transform: none;
          }

          &:hover {
            background-color: transparent;

            svg path {
              fill: #787878;
            }
          }

          svg path {
            fill: #787878;
          }
        }
      }

      b {
        font-size: 18px;
      }
    }

    &-price {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 33%;

      @media(max-width: 885px) {
        width: 100px;
      }

      b {
        font-weight: bold;
        font-size: 19px;
        letter-spacing: 0.01em;
      }
    }

    &-remove {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 4%;

      @media(max-width: 600px) {
        position: absolute;
        right: 0;
        top: 50%;
        margin-top: -16px;
      }

      .button {
        border-color: darken($color: $grey, $amount: 10);
      }

      svg {
        transform: rotate(45deg);

        path {
          fill: darken($color: $grey, $amount: 15);
        }
      }

      .button {
        svg {
          width: 11.5px;
          height: 11.5px;
          position: relative;
        }

        &:hover,
        &:active {
          border-color: darken($color: $grey, $amount: 80);
          background-color: rgb(247, 73, 73);
        }
      }
    }
  }

  &__right {
    display: flex;
    width: 50%;

    @media(max-width: 600px) {
      margin-bottom: 10px;
      width: 80%;
    }

    @media(max-width: 500px) {
      margin-right: auto;
      width: 90%;
    }
  }

  &__bottom {
    margin: 50px 0;

    &-details {
      display: flex;
      justify-content: space-between;

      @media(max-width: 510px) {
        flex-direction: column;
        align-items: center;
      }

      span:first-child {
        margin-bottom: 7px;
      }

      span {
        font-size: 19px;

        &:last-of-type {
          b {
            color: $violet;
          }
        }
      }
    }

    &-buttons {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;

      .go-back-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 215px;

        border-color: darken($color: $grey, $amount: 10);

        @media(max-width: 510px) {
          margin-right: 7px;
        }

        span {
          color: darken($color: $grey, $amount: 20);
          font-weight: 500;
          font-weight: 600;
        }

        &:hover {
          background-color: darken($color: $grey, $amount: 90);
          border-color: darken($color: $grey, $amount: 90);

          span {
            color: $grey;
          }
        }

        svg {
          margin-right: 12px;

          path {
            fill: transparent;
            stroke-width: 2;
          }
        }
      }

      .pay-btn {
        font-size: 16px;
        font-weight: 600;
        width: 210px;
        padding: 16px;

        @media(max-width: 510px) {
          margin-left: 7px;
        }
      }
    }
  }

  &--empty {
    margin: 80px auto;
    max-width: 620px;
    text-align: center;

    @media(max-width: 720px) {
      max-width: 80%;
    }

    @media(max-width: 450px) {
      margin: 40px auto;
    }

    &__icon {
      @media(max-width: 439px) {
        display: block;
        margin-top: 16px;
      }
    }

    h2 {
      font-size: 36px;
      margin-bottom: 15px;
      font-weight: 700;
    }

    p {
      font-size: 20px;
      line-height: 145.4%;
      letter-spacing: 0.01em;
      color: #777777;
    }

    icon {
      position: relative;
      top: 2px;
    }

    img {
      display: block;
      max-width: 300px;
      margin: 45px auto 60px;

      @media(max-width: 410px) {
        width: 90%;
      }
    }

    .button--black {
      padding: 12px 0 14px;
      width: 230px;
      margin: 0 auto;
      font-weight: 600;
      font-size: 18px;
    }
  }
}

.not-found {
  padding: 100px;
  text-align: center;
  max-width: 750px;
  margin: 0 auto;

  @media(max-width: 750px) {
    padding: 20px;
  }

  h1 {
    @media(max-width: 700px) {
      font-size: 39px;
    }
  }

  img {
    max-width: 500px;
    display: block;
    margin: 0 auto;

    @media(max-width: 750px) {
      width: 100%;
    }
  }

  span {
    font-size: 69px;
  }

  &__text {
    font-size: 22px;
  }
}

.error--block {
  max-width: 500px;
  padding: 100px 50px;
  margin: 0 auto;
  text-align: center;

  @media(max-width: 650px) {
    padding: 70px 20px;
  }

  &__title {
    font-size: 35px;
    margin-bottom: 20px;
  }

  &__text {
    font-size: 17px;
  }

  img {
    max-width: 400px;
    margin: 40px auto;

    @media(max-width: 650px) {
      width: 100%;
    }
  }
}